import { Component, OnInit, Input, OnChanges, SimpleChanges, SimpleChange } from '@angular/core';
import { EbeaconServiceService } from 'src/app/service/ebeacon-service.service';

@Component({
  selector: 'app-messages',
  templateUrl: './messages.component.html',
  styleUrls: ['./messages.component.scss']
})
export class MessagesComponent implements OnChanges, OnInit {
  messages: any[];
   private _discussionId: string;
  //@Input() discussionId: string;

  get discussionId(): string {
   return this._discussionId;
 }

 @Input()
  set discussionId(discussionId: string) {
    console.log('prev value: ', this._discussionId);
    console.log('got name: ', discussionId);
    this._discussionId = discussionId;
  }

  constructor(private ebeaconService: EbeaconServiceService ) { }

  ngOnInit() {
    this.getMessages(this.discussionId);
  }

  getMessages = (discussionId: string) => {
    alert("Getting messages for "+discussionId);
    this.ebeaconService.getMessages(discussionId).subscribe(
      res => {
        alert(`The returned message is ${res}`);
      },
      err => {
        this.messages = [{
          owner : "Praveen",
          text : "This is Praveen's message"
        },
        {
          owner : "Unni",
          text : "This is Unni's message"
        },
        {
          owner : "Sandiya",
          text : "This is Sandiya's message"
        },
        {
          owner : "Ram",
          text : "This is Ram's message"
        }];
        alert(`Something went error ${err}`);
      }
    );
  }

  ngOnChanges(changes: SimpleChanges) {
    const discussionId: SimpleChange = changes.discussionId;
    console.log('prev value: ', discussionId.previousValue);
    console.log('got name: ', discussionId.currentValue);
  }


}
