import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';

@Injectable({
  providedIn: 'root'
})
export class EbeaconServiceService {

  messages: any[] = [{
    owner : "Praveen",
    text : "This is Praveen's message"
  },
  {
    owner : "Unni",
    text : "This is Unni's message"
  },
  {
    owner : "Sandiya",
    text : "This is Sandiya's message"
  },
  {
    owner : "Ram",
    text : "This is Ram's message"
  }];

PUBLISH_MESSAGE_URL:string = "https://us-central1-us-con-gcp-sbx-0000118-022619.cloudfunctions.net/publishMessage";
GET_USER_DISCUSSION_URL:string = "https://us-central1-us-con-gcp-sbx-0000118-022619.cloudfunctions.net/publishMessage?userId='firstname.lastname'";
GET_MESSAGES_URL:string = "https://us-central1-us-con-gcp-sbx-0000118-022619.cloudfunctions.net/publishMessage";

  constructor(private http: HttpClient) { }

  public publishMessage = (message: string, discussionId:String ) => {
    return this.http.post(this.PUBLISH_MESSAGE_URL, {message : message, options : {discussionId : discussionId}});
   }

  public loadMessages = ():any[] => {
    return this.messages;
  }

  public getUserDiscussion = () => {
    return this.http.get(this.GET_USER_DISCUSSION_URL);
   }

   public getMessages = (discussionId: string) => {
     return this.http.get(this.GET_MESSAGES_URL+"?discussionId="+discussionId);
   }
}
