import { Component, OnInit } from '@angular/core';
import { EbeaconServiceService } from 'src/app/service/ebeacon-service.service';
import {FormControl} from '@angular/forms';
import { MatSnackBarComponent } from 'src/app/mat-snack-bar/mat-snack-bar/mat-snack-bar.component';

@Component({
  selector: 'app-message-home',
  templateUrl: './message-home.component.html',
  styleUrls: ['./message-home.component.scss']
})
export class MessageHomeComponent implements OnInit {
  inputMessage: any = {text : ""};
  mySelectControl = new FormControl();
  selectedDiscussion: string;
  showDiscussion: boolean= false;
  showNewMessage : boolean = false;
  discussionList:
  { id: number, name: string }[] = [
    { "id": 1, "name": "Discussion 1" },
    { "id": 2, "name": "Discussion 2" },
    { "id": 3, "name": "Discussion 3" }
  ];
    messages: any[];

  constructor(private eBeaconService: EbeaconServiceService, private snackBar: MatSnackBarComponent) { }

  ngOnInit() {
    this.getUserDiscussions();
    this.mySelectControl.valueChanges.subscribe(value => {
        this.showHideMessages(value);
    });
  }

  showHideMessages(value:string){
    this.selectedDiscussion = value;
    if(value == "none") {
      this.showNewMessage = false;
      this.showDiscussion= false;
    } else if (value == "new") {
      this.showNewMessage = true;
      this.showDiscussion= false;
    } else {
      this.showNewMessage = true;
      this.showDiscussion= true;
      this.getMessages(value);
    }
  }

  getUserDiscussions = () => {
    this.eBeaconService.getUserDiscussion().subscribe(
      res => {
        alert(`The returned message is ${res}`);
      },
      err => {
        alert(`Something went error ${err}`);
      }
    );
  }

  getMessages = (discussionId: string) => {
    this.eBeaconService.getMessages(discussionId).subscribe(
      res => {
        alert(`The returned message is ${res}`);
      },
      err => {
        if(discussionId == '1') {
          this.messages = [{
            owner : "firstname.lastname",
            text : "Discussion1 - Message1"
          },
          {
            owner : "Subcriber1",
            text : "Discussion1 - Response1"
          }];
        } else if (discussionId == '2') {
          this.messages = [{
            owner : "firstname.lastname",
            text : "Discussion2 - Message1"
          },
          {
            owner : "Subcriber2",
            text : "Discussion2 - Response1"
          }];
        } else if (discussionId == '3') {
          this.messages = [{
            owner : "firstname.lastname",
            text : "Discussion3 - Message1"
          }];
        }
        alert(`Something went error ${err}`);
      }
    );
  }

  public publishMessage = () => {
    //this.eBeaconService.messages.unshift(this.inputMessage);
    this.eBeaconService.publishMessage(this.inputMessage.text, this.selectedDiscussion).subscribe(
      res => {
        this.snackBar.openSnackBar( "Input saved successfully", 'Close', "green-snackbar");
      },
      err => {
        this.snackBar.openSnackBar( "Error fetching task details. Please try again later", 'Close', "red-snackbar");
      }
    );
  }

}

export class Discussion {
  id: string;
  title: string;
}
